<template lang="html">
  <div>
     : {{result.length}} 
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: null
    };
  },
  /*
    params    $route  .
     watch: {
       '$route' (to, from) {
         //   ...
       }
     }
  */
  watch: {
    $route(to, from) {
      this.result = JSON.parse(sessionStorage.getItem("result"));
    }
  },
  created() {
    this.result = JSON.parse(sessionStorage.getItem("result"));
  }
};
</script>

<style lang="css">
</style>



