<template>
  <div v-if="select">
    <b-modal id="modal1" title="" hide-footer>
      <h3 class="text-left"> </h3>
      <table class="table" v-if="data">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{data.name}}</td>
            <td>{{data.phone}}</td>
            <td>{{data.email}}</td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left">  </h3>
      <table class="table" v-if="prop">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{prop.name}}</td>
            <td>{{prop.phone}}</td>
            <td>{{prop.address}}</td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left"></h3>
      <table class="table" v-if="info">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{info.price}} </td>
            <td>{{info.charge!='0'?info.charge+' ':''}}</td>
            <td>{{info.total}} </td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left"> : {{select.radioSelected}}</h3>
      <table class="table" v-if="select.radioSelected=='card'">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{select.cardSelected}}</td>
            <td>{{select.halbuSelected}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='mutong'">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{select.bankSelected}}</td>
            <td>{{select.date}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='account'">
        <thead>
          <tr>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{select.bankSelected}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='phone'">
        <thead>
          <tr>
            <th> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{select.phoneSelected | uppercase}}</td>
          </tr>
        </tbody>
      </table>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: ["prop"],
  data() {
    return {
      info: null,
      data: null,
      select: null
    };
  },
  created() {
    this.$bus.$on("method", data => {
      this.select = data;
    });
    this.data = JSON.parse(sessionStorage.getItem("user"));
    this.info = JSON.parse(sessionStorage.getItem("info"));
  },
  filters: {
    uppercase: function(v) {
      if (v) {
        return v.toUpperCase();
      }
    }
  }
};
</script>

<style>
</style>



